// 
export const useFormatAmount = (money, symbol = "", decimals = 2) => {
  const formatToFixed = (money, decimals = 2) => {
    return (Math.round((parseFloat(money) + Number.EPSILON) * Math.pow(10, decimals)) / Math.pow(10, decimals)).toFixed(
      decimals
    )
  }
  let arr = formatToFixed(money, decimals).toString().split(".")
  let num = arr[0]
  let first = ""
  while (num.length > 3) {
    first = "," + num.slice(-3) + first
    num = num.slice(0, num.length - 3)
  }
  if (num) {
    first = num + first
  }
  return `${symbol} ${[first, arr[1]].join(".")}`
}

// 
export const useRandomColor = () => {
  return "#" + ((Math.random() * 0xffffff) << 0).toString(16)
}

// 
export const useRandomGradienColor = () => {
  let r = Math.floor(Math.random() * 255)
  let g = Math.floor(Math.random() * 255)
  let b = Math.floor(Math.random() * 255)

  // 
  var startColor = "rgba(" + r + "," + g + "," + b + ",1)"
  var endColor = "rgba(" + r + "," + g + "," + b + ",0.6)"

  // 
  var gradient = "linear-gradient(to right, " + startColor + ", " + endColor + ")"

  return gradient
  // return "rgba(" + r + "," + g + "," + b + ",0.8)"
}

// pick
export const usePick = (obj, keys) => {
  return keys.reduce((acc, key) => {
    if (obj.hasOwnProperty(key)) {
      acc[key] = obj[key];
    }
    return acc;
  }, {});
}

// 
export const useToTree = (list) => {
  // 1. 
  const treeList = [] // 
  let map = {} // 

  // 2. ,children.
  // : id
  // children
  list.forEach(item => {
    if (!item.children) {
      item.children = []
    }
    map[item.id] = item
  })
  // 
  list.forEach(item => {
    // 
    //    children
    //     treeList
    const parent = map[item.parentId]
    // item
    if (parent) {
      parent.children.push(item)
    } else {
      //  ,
      treeList.push(item)
    }
  })
  // 
  return treeList
}

let utils = {
  useFormatAmount,
  useRandomColor,
  useRandomGradienColor,
  usePick,
  useToTree
}

export default utils
