export const useDrag = className => {
  const dom = document.querySelector(className)
  let x = 0
  let y = 0
  let l = 0
  let t = 0
  let isDown = false
  //
  dom.onmousedown = function (e) {
    //
    e.preventDefault && e.preventDefault()

    //xy
    x = e.clientX
    y = e.clientY

    //
    l = dom.offsetLeft
    t = dom.offsetTop

    dom.onmousemove = function (e) {
      //xy
      let nx = e.clientX
      let ny = e.clientY
      //
      let nl = nx - (x - l)
      let nt = ny - (y - t)
      dom.style.left = nl + "px"
      dom.style.top = nt + "px"
    }

    dom.onmouseout = e => {
      dom.onmousemove = null
    }
  }

  dom.onmouseup = function () {
    dom.onmousemove = null
  }
}

// 
export const useDragSort = {
  dragIndex: 0,
  start: (e, index) => {
    e.stopPropagation()
    useDragSort.dragIndex = index
    setTimeout(() => {
      e.target.classList.add("moveing")
    }, 0)
  },
  enter: (e, index, dragList) => {
    e.preventDefault()
    // 
    if (useDragSort.dragIndex !== index) {
      const source = dragList[useDragSort.dragIndex]
      dragList.splice(useDragSort.dragIndex, 1)
      dragList.splice(index, 0, source)
      // 
      useDragSort.dragIndex = index
    }
  },
  over: e => {
    e.preventDefault()
    e.dataTransfer.dropEffect = "move"
  },
  end: e => {
    e.target.classList.remove("moveing")
  }
}

// 
export const useRotate = (className, rotateDeg) => {
  let dom = document.querySelector(className)
  dom.style.transform = `rotate(${rotateDeg}deg)`
}

// 
export const useScale = (className, rotateScale) => {
  let dom = document.querySelector(className)
  dom.style.transform = `scale(${rotateScale})`
}

// 
export const useDebounce = (func, delay, immediate = true) => {
  let timeoutId // ID

  // 
  return function (...args) {
    const context = this // this
    const later = () => {
      timeoutId = null // ID
      if (!immediate) {
        func.apply(context, args) // 
      }
    }

    clearTimeout(timeoutId) // 

    if (immediate && !timeoutId) {
      // 
      func.apply(context, args) // 
    }

    timeoutId = setTimeout(later, delay) // later
  }
}

// 
export const useThrottle = (func, delay) => {
  let wait = false
  return (...args) => {
    if (wait) {
      return
    }

    func(...args)
    wait = true
    setTimeout(() => {
      wait = false
    }, delay)
  }
}

// 
export const useOnce = func => {
  let ran = false
  let result
  return function () {
    if (ran) return result
    result = func.apply(this, arguments)
    ran = true
    return result
  }
}

// 
export const useCopyText = text => {
  var input = document.createElement("input")
  input.setAttribute("value", text)
  document.body.appendChild(input)
  input.select()
  document.execCommand("copy")
  document.body.removeChild(input)
}

import { Watermark as useWatermark } from "watermark-js-plus"

let hooks = {
  useDrag,
  useRotate,
  useScale,
  useDebounce,
  useThrottle,
  useOnce,
  useCopyText,
  useWatermark,
  useDragSort
}

export default hooks
